export default {
  layout: 'admin',
  data() {
    return {
      results: [],
      listLoading: false,
      page: {},
      filters: {
        id: ''
      },
      selectedRows: [],

      addForm: {
        username: '',
        nickname: '',
        avatar: '',
        email: '',
        roles: [],
        password: '',
        status: ''
      },
      addFormVisible: false,
      addFormRules: {},
      addLoading: false,

      editForm: {
        id: '',
        username: '',
        nickname: '',
        avatar: '',
        email: '',
        roles: [],
        password: '',
        status: ''
      },
      editFormVisible: false,
      editFormRules: {},
      editLoading: false,

      forbiddenForm: {
        userId: '',
        days: 0,
        reason: ''
      },
      forbiddenFormVisible: false,
      forbiddenLoading: false
    }
  },
  mounted() {
    this.list()
  },
  methods: {
    list() {
      const me = this
      me.listLoading = true
      const params = Object.assign(me.filters, {
        page: me.page.page,
        limit: me.page.limit
      })
      this.$axios
        .post('/api/admin/user/list', params)
        .then((data) => {
          me.results = data.results
          me.page = data.page
        })
        .finally(() => {
          me.listLoading = false
        })
    },
    handlePageChange(val) {
      this.page.page = val
      this.list()
    },
    handleLimitChange(val) {
      this.page.limit = val
      this.list()
    },
    handleAdd() {
      this.addForm = {
        name: '',
        description: ''
      }
      this.addFormVisible = true
    },
    addSubmit() {
      const me = this
      this.$axios
        .post('/api/admin/user/create', this.addForm)
        .then((data) => {
          me.$message({ message: '提交成功', type: 'success' })
          me.addFormVisible = false
          me.list()
        })
        .catch((rsp) => {
          me.$notify.error({ title: '错误', message: rsp.message })
        })
    },
    handleEdit(index, row) {
      const me = this
      this.$axios
        .get(`/api/admin/user/${row.id}`)
        .then((data) => {
          me.editForm = Object.assign({}, data)
          me.editForm.password = ''
          me.editFormVisible = true
        })
        .catch((rsp) => {
          me.$notify.error({ title: '错误', message: rsp.message })
        })
    },
    editSubmit() {
      const params = { ...this.editForm }
      if (params.roles && params.roles.length) {
        params.roles = params.roles.join(',')
      } else {
        params.roles = ''
      }
      const me = this
      this.$axios
        .post('/api/admin/user/update', params)
        .then((data) => {
          me.list()
          me.editFormVisible = false
        })
        .catch((rsp) => {
          me.$notify.error({ title: '错误', message: rsp.message })
        })
    },
    handleSelectionChange(val) {
      this.selectedRows = val
    },
    showForbiddenDialog(index, row) {
      this.forbiddenForm = {
        userId: row.id,
        days: 7,
        reason: '广告'
      }
      this.forbiddenFormVisible = true
    },
    async forbidden() {
      this.forbiddenLoading = true
      try {
        await this.$axios.post('/api/admin/user/forbidden', this.forbiddenForm)
        this.forbiddenForm = {}
        this.forbiddenFormVisible = false
        this.$message.success('禁言成功')
        this.list()
      } catch (e) {
        this.$message.success('禁言失败 ' + (e.message || e))
      } finally {
        this.forbiddenLoading = false
      }
    },
    async removeForbidden(index, row) {
      try {
        await this.$axios.post('/api/admin/user/forbidden', {
          userId: row.id,
          days: 0
        })
        this.$message.success('取消禁言成功')
        this.list()
      } catch (e) {
        this.$message.success('取消禁言失败 ' + (e.message || e))
      }
    }
  }
}
